	module.exports = {"data":{"markdownRemark":{"html":"<p>There must be at least 3 dashes separating each header cell.\nThe outer pipes (|) are optional, and you don't need to make the\nraw Markdown line up prettily. You can also use inline Markdown.</p>\n<p><img src=\"https://d2gg9evh47fn9z.cloudfront.net/800px_COLOURBOX18211661.jpg\" alt=\"Flowers\" title=\"Title\"></p>\n<p>Colons can be used to align columns.</p>\n<table>\n<thead>\n<tr>\n<th>Tables</th>\n<th align=\"center\">Are</th>\n<th align=\"right\">Cool</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>col 3 is</td>\n<td align=\"center\">right-aligned</td>\n<td align=\"right\">$1600</td>\n</tr>\n<tr>\n<td>col 2 is</td>\n<td align=\"center\">centered</td>\n<td align=\"right\">$12</td>\n</tr>\n<tr>\n<td>zebra stripes</td>\n<td align=\"center\">are neat</td>\n<td align=\"right\">$1</td>\n</tr>\n</tbody>\n</table>\n<p>There must be at least 3 dashes separating each header cell.\nThe outer pipes (|) are optional, and you don't need to make the\nraw Markdown line up prettily. You can also use inline Markdown.</p>\n<table>\n<thead>\n<tr>\n<th>Markdown</th>\n<th>Less</th>\n<th>Pretty</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><em>Still</em></td>\n<td><code>renders</code></td>\n<td><strong>nicely</strong></td>\n</tr>\n<tr>\n<td>1</td>\n<td>2</td>\n<td>3</td>\n</tr>\n</tbody>\n</table>\n<ul>\n<li class=\"task-list-item\"><input type=\"checkbox\" checked disabled> @mentions, #refs, <a href=\"\">links</a>, <strong>formatting</strong>, and <del>tags</del> supported</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" checked disabled> list syntax required (any unordered or ordered list supported)</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" checked disabled> this is a complete item</li>\n<li class=\"task-list-item\"><input type=\"checkbox\" disabled> this is an incomplete item</li>\n</ul>\n<blockquote>\n<p>É impossível para um homem ser enganado por outra pessoa que não seja ele próprio.</p>\n<p>-- <cite>Ralph Waldo Emerson</cite></p>\n</blockquote>\n<p>A <a href=\"http://example.com\" title=\"Title\">link</a>.</p>\n<p>I have more <sup id=\"fnref-1\"><a href=\"#fn-1\" class=\"footnote-ref\">1</a></sup> to say up here.</p>\n<ol>\n<li>Item</li>\n<li>\n<p>Item</p>\n<ul>\n<li>Mixed</li>\n<li>Mixed  </li>\n</ul>\n</li>\n<li>Item1. Item 2. </li>\n</ol>\n<pre><code class=\"language-ruby\">require 'redcarpet'\nmarkdown = Redcarpet.new(\"Hello World!\")\nputs markdown.to_html\n</code></pre>\n<div class=\"footnotes\">\n<hr>\n<ol>\n<li id=\"fn-1\">\n<p>To say down here.I have mo</p>\n<a href=\"#fnref-1\" class=\"footnote-backref\">↩</a>\n</li>\n</ol>\n</div>","frontmatter":{"date":"November 07, 2017","path":"/blog/super-cool-thing","title":"super cool thing"}}},"pathContext":{}}

